<template>
	<div class="news">
		<div class="auto-news">
			<mt-swipe :auto="4000">
			  	<mt-swipe-item >
			  		<img src="../../assets/auto1.jpg" alt="">
			  		<span><i class="iconfont icon-remen"></i> 90</span>
			  	</mt-swipe-item>
			  	<mt-swipe-item>
			  		<img src="../../assets/auto2.jpg" alt="">
			  		<span><i class="iconfont icon-xinwen"></i> </span>
			  	</mt-swipe-item>
			  	<mt-swipe-item>
			  		<img src="../../assets/auto3.jpg" alt="">
			  		<span><i class="iconfont icon-remen"></i> </span>
			  	</mt-swipe-item>
			  	<mt-swipe-item>
			  		<img src="../../assets/auto4.jpg" alt="">
			  		<span><i class="iconfont icon-xinwen"></i></span>
			  	</mt-swipe-item>
			</mt-swipe>
		</div>
		
		<ul class="news-ul">
			<li v-for="v in list">
				<div class="news-left">
					<h3>{{v.title}}</h3>
					<span>{{v.tcount}}</span>
					<span>{{v.source}}</span><br>
					<span>{{v.ptime}}</span>
				</div>
				<div class="news-right">
					<img :src="v.picInfo[0].url">
				</div>
			</li>
		</ul>
		<div class="news-bottom">
			<h2>(>^^<)</h2>
		</div>
	</div>
</template>

<script>
	export default{
		// name:'news',
		data(){
			return{
				msg:``,
				list:[]
			}
		},
		mounted(){
			var url='data/news.json';
    		this.$axios.get(url).then(response=>{
    			// console.log(response)
      			this.list=response.data.news;
      		}).catch(error=>{
      			console.log(error);
      		})
		}
	}
</script>

<style scoped=''>
	.news{
		margin-bottom: 50px;
	}
	.news-ul{
		padding: 0 15px;
		list-style: none;
		border-top: 1px solid #ddd8ce;
		margin-top: 15px;
		background: #fefefe;
		box-shadow: 0 0 15px #ddd8ce;
	}
	.news-ul li{
		border-bottom: 1px solid #999;
		padding: 5px;
		display: flex;
		justify-content:center;
    	align-items:center;
    	/* flex */
	}
	.news-right{
		width: 30%;
		display: table-cell;
		vertical-align: middle;
	}
	.news-right img{
		width: 100%;
	}
	.news-left{
		width: 70%;
	}
	.news-left h3{
		font-weight: normal;
	}
	.news-left span{
		font-size: 0.12rem;
		color: #999;
	}
	.news-bottom{
		text-align: center;
	}
</style>


