<template>
	<div class="hot">
		<ul class="hot-ul">
			<li v-for='v in hot'>
				<img :src="v.picInfo[0].url" alt="">
				<h3><i class="iconfont icon-remen"></i>{{v.title}} <span>[]</span></h3>
				<div class="ul-span">
					<span>{{v.tcount}}</span>
					<span>{{v.ptime}}</span>
					<span>{{v.category}}</span>
				</div>
			</li>
		</ul>
		<div class="news-bottom">
			<h2>(>^^<)</h2>
		</div>
	</div>
	
</template>

<script>
	export default{
		data(){
			return{
				hot:[]
			}
		},
		mounted(){
			var url='data/news.json';
			this.$axios.get(url).then(response=>{
    			// console.log(response.data.list)
      			this.hot=response.data.list;
      		}).catch(error=>{
      			console.log(error);
      		})
		}
		
	}
</script>

<style>
	.hot{
		margin-bottom: 50px;
	}
	.hot-ul{
		width: 100%;
	}
	.hot-ul li{
		max-width: 700px;
		display: block;
		margin: 0 auto;
		padding: 10px;
		border-bottom: 1px solid #999;
	}
	.hot-ul li img{
		display: block;
		margin: 0 auto;
		width: 80%;
	}
	.hot-ul li h3{
		font-size: .16rem;
		margin-bottom: 10px;
		margin-top: 10px;
	}
	.hot-ul li h3 span{
		color: #ff4241;
	}
	.hot-ul .icon-remen{
		font-size: .25rem;
		color: red;
		font-weight: normal;
	}
	.ul-span{
		display: flex;
	}
	.ul-span span{
		font-size: .12rem;
		color: #666;
	}
	.ul-span span:nth-of-type(1){
		margin-left: 30px;
		margin-right: 5px;
	}
	.ul-span span:nth-of-type(2){
		flex-grow: 1;
	}
	.ul-span span:nth-of-type(3){
		margin-right: 10%;
	}
	.news-bottom{
		text-align: center;
	}
</style>


